table-dark-bg;\n\n  th,\n  td,\n  thead th {\n    border-color: $table-dark-border-color;\n  }\n\n  &.table-bordered {\n    border: 0;\n  }\n\n  &.table-striped {\n    tbody tr:nth-of-type(odd) {\n      background-color: $table-dark-accent-bg;\n    }\n  }\n\n  &.table-hover {\n    tbody tr {\n      @include hover {\n        background-color: $table-dark-hover-bg;\n      }\n    }\n  }\n}\n\n\n// Responsive tables\n//\n// Generate series of `.table-responsive-*` classes for configuring the screen\n// size of where your table will overflow.\n\n.table-responsive {\n  @each $breakpoint in map-keys($grid-breakpoints) {\n    $next: breakpoint-next($breakpoint, $grid-breakpoints);\n    $infix: breakpoint-infix($next, $grid-breakpoints);\n\n    &#{$infix} {\n      @include media-breakpoint-down($breakpoint) {\n        display: block;\n        width: 100%;\n        overflow-x: auto;\n        -webkit-overflow-scrolling: touch;\n        -ms-overflow-style: -ms-autohiding-scrollbar; // See https://github.com/twbs/bootstrap/pull/10057\n\n        // Prevent double border on horizontal scroll due to use of `display: block;`\n        > .table-bordered {\n          border: 0;\n        }\n      }\n    }\n  }\n}\n","// Tables\n\n@mixin table-row-variant($state, $background, $border: null) {\n  // Exact selectors below required to override `.table-striped` and prevent\n  // inheritance to nested tables.\n  .table-#{$state} {\n    &,\n    > th,\n    > td {\n      background-color: $background;\n    }\n\n    @if $border != null {\n      th,\n      td,\n      thead th,\n      tbody + tbody {\n        border-color: $border;\n      }\n    }\n  }\n\n  // Hover states for `.table-hover`\n  // Note: this is not available for cells or rows within `thead` or `tfoot`.\n  .table-hover {\n    $hover-background: darken($background, 5%);\n\n    .table-#{$state} {\n      @include hover {\n        background-color: $hover-background;\n\n        > td,\n        > th {\n          background-color: $hover-background;\n        }\n      }\n    }\n  }\n}\n","@import '../../theme';\n@import '~bootstrap/scss/tables';\n\n.table {\n  thead {\n    th {\n      border-bottom: 0 none;\n      border-top: none;\n      font-size: $small-font-size;\n      font-weight: $font-weight-semi;\n      text-transform: uppercase;\n      color: theme-color('secondary');\n      text-align: center;\n    }\n  }\n  td {\n    vertical-align: middle;\n  }\n  &-hover {\n    tbody {\n      tr {\n        min-height: 74px;\n        &:hover {\n          background-color: theme-color('background');\n        }\n      }\n    }\n  }\n  &--mobile {\n    font-weight: $font-weight-bold;\n    display: none;\n    @include media-breakpoint-down(sm) {\n      display: inline-flex;\n    }\n  }\n\n  &-close {\n    position: relative;\n    right: 18px;\n    width: 18px;\n    height: 18px;\n\n    &:hover {\n      opacity: 1;\n    }\n    &:before,\n    &:after {\n      position: absolute;\n      left: 15px;\n      content: ' ';\n      height: 18px;\n      width: 2px;\n      background-color: theme-color('secondary');\n      top: 30%;\n    }\n    &:before {\n      transform: rotate(45deg);\n    }\n    &:after {\n      transform: rotate(-45deg);\n    }\n  }\n}\n","@import '../../theme';\n@import '~bootstrap/scss/card';\n\n.card {\n  border-radius: 0;\n}\n\n.card-body {\n  padding: 20px;\n}\n\n.card-body-title {\n  padding-bottom: 10px;\n  @include type('4');\n}\n\n.card-header {\n  background-color: var(--cx-color-secondary);\n  @include type('8');\n  color: var(--cx-color-inverse);\n  line-height: 20px;\n  padding-top: 5px;\n  padding-bottom: 5px;\n  border-radius: 0;\n\n  &:first-child {\n    border-radius: 0;\n  }\n}\n\n.card-actions-container {\n  display: flex;\n  justify-content: flex-end;\n  padding: 1.25rem 0 0 0;\n}\n\n.card-label-bold {\n  font-weight: var(--cx-font-weight-bold);\n}\n\n.card-link {\n  @include type('8');\n}\n","@import '../../theme';\n@import '../../functions';\n@import '../../mixins';\n.accordion {\n  .card {\n    margin-bottom: 20px;\n    border: none;\n    &-header {\n      background-color: theme-color('background');\n      padding: 25px 20px;\n      border: none;\n      h5 {\n        font-size: $font-size-base * 1.375;\n        font-weight: 600;\n        color: theme-color('text');\n        .btn {\n          text-align: start;\n          text-decoration: none !important;\n          width: 100%;\n          font-weight: 600;\n          &:hover {\n            text-decoration: none;\n          }\n          &:after {\n            float: right;\n            font-size: 33px;\n            bottom: 12px;\n            position: relative;\n          }\n          &[aria-expanded='false'] {\n            &:after {\n              content: '+';\n            }\n          }\n          &[aria-expanded='true'] {\n            color: theme-color('primary');\n            &:after {\n              content: '\\2013';\n            }\n          }\n        }\n      }\n    }\n    &-body {\n      padding: 30px 0 20px 0;\n    }\n  }\n}\n\n//cx-accordion is an alterative version of the accordion\n//media queries are to be added in each component\n//ex. anonymus consent dialog\n.cx-accordion {\n  margin: 5px auto;\n  line-height: 1.6;\n  list-style-type: none;\n  padding: 0;\n\n  .cx-accordion-item {\n    border: 1px solid var(--cx-color-light);\n    position: relative;\n    margin-top: 10px;\n    margin-bottom: 10px;\n\n    .cx-accordion-tab {\n      background-color: var(--cx-color-inverse);\n      &:hover {\n        cursor: pointer;\n      }\n      .cx-accordion-title {\n        padding-top: 15px;\n        padding-inline-end: 15px;\n        padding-bottom: 15px;\n        padding-inline-start: 50px;\n        display: block;\n        position: relative;\n        font-weight: 600;\n      }\n    }\n\n    .cx-accordion-tabpanel {\n      transition: height 0.3s ease-out;\n      height: 0;\n      overflow: hidden;\n      background-color: var(--cx-color-inverse);\n\n      .cx-accordion-content {\n        padding-top: 0;\n        padding-inline-end: 40px;\n        padding-bottom: 10px;\n        padding-inline-start: 40px;\n      }\n    }\n  }\n}\n","@import '../../theme';\n\nul[role='tablist'] {\n  .nav-link {\n    border: none;\n    &.active {\n      background-color: var(--cx-color-inverse);\n      color: var(--cx-color-primary);\n      border-width: 0 0 5px 0;\n      border-style: solid;\n\n      div {\n        color: var(--cx-color-primary);\n        font-weight: 600;\n      }\n    }\n  }\n}\n","@import '../../theme';\n@import '../../mixins';\n\n// None of the styles here below are customizable at component level\n// analyze how deep we want to customize these\n\n.carousel-indicators {\n  bottom: 0;\n  margin-bottom: 0;\n\n  li {\n    margin-inline-start: 7px;\n    @include media-breakpoint-up(xl) {\n      margin-inline-start: 25px;\n    }\n  }\n\n  > .active {\n    background-color: theme-color('primary');\n  }\n\n  > li {\n    background-color: theme-color('light');\n    width: 12px;\n    height: 12px;\n    border-radius: 50%;\n    @include media-breakpoint-up(xl) {\n      width: 18px;\n      height: 18px;\n    }\n  }\n}\n\n.carousel-inner {\n  margin-inline-start: auto;\n  margin-inline-end: auto;\n  @include media-breakpoint-up(lg) {\n    margin-inline-start: 30px;\n    margin-inline-end: 30px;\n  }\n}\n","a {\n  color: var(--cx-color-primary);\n\n  &:hover {\n    color: var(--cx-color-primary);\n  }\n\n  &.disabled {\n    pointer-events: var(--cx-pointer-event, none !important);\n    cursor: var(--cx-cursor, not-allowed !important);\n    opacity: 0.8;\n  }\n}\n\n.cx-action-link {\n  @include type('7');\n  text-transform: capitalize;\n  text-decoration: underline;\n  cursor: pointer;\n  display: inline-block;\n  &:hover {\n    color: var(--cx-color-primary);\n    text-decoration: none;\n  }\n}\n","@import '../../theme';\n@import '~bootstrap/scss/dropdown';\n\n$cx-dropdown-toggle-border-color: 'inverse' !important;\n$cx-dropdown-toggle-border-hover-color: 'primary' !default;\n\n$cx-dropdown-toggle-border-width: 0 2px 2px 0 !default;\n\n.dropdown-menu {\n  border-radius: 0;\n  margin-top: 11px;\n}\n\n.ng-dropdown-panel .ng-dropdown-panel-items .ng-option.ng-option-selected {\n  background-color: transparent;\n}\n","@import '../../theme';\n@import '../../functions';\n@import '../../mixins';\n\n.cx-app {\n  &__breadcrumb {\n    background-color: theme-color('background');\n    text-align: center;\n    padding-top: 25px;\n  }\n}\n","@import '../../theme';\n\n.cx-page {\n  padding-bottom: 120px;\n}\n\n.cx-page-header {\n  background-color: var(--cx-color-background);\n  text-align: center;\n  padding: 25px 0;\n}\n\n.cx-page-title {\n  @include type('4');\n  font-weight: var(--cx-font-weight-semi);\n  margin: 0 auto;\n}\n\n.cx-page-section {\n  padding-top: 60px;\n}\n","@import '../../theme';\n\n.cx-section {\n  margin-bottom: var(--cx-margin-bottom, 50px);\n  &:last-child {\n    margin-bottom: var(--cx-margin-bottom, 0px);\n  }\n}\n\n.cx-section-title {\n  @include type('4');\n  font-weight: var(--cx-font-weight, var(--cx-font-weight-semi));\n  margin-bottom: var(--cx-margin-bottom, 25px);\n  text-transform: var(--cx-text-transform, none);\n}\n\n.cx-section-title-alt {\n  text-transform: var(--cx-text-transform, none);\n}\n",".cx-notice {\n  padding: 32px 0;\n  text-align: center;\n  @include type();\n  color: var(--cx-color-dark);\n  background-color: var(--cx-color-inverse);\n  margin-bottom: -1.5rem;\n}\n","@keyframes spin {\n  100% {\n    transform: rotate(360deg);\n  }\n}\n\n:root {\n  // spinner size is added to the root so that it can be overridden on individual elements\n  --cx-spinner-size: 40px;\n}\n\n%spinner {\n  content: '';\n  box-sizing: border-box;\n  position: absolute;\n  top: 50%;\n  left: 50%;\n  width: var(--cx-spinner-size);\n  height: var(--cx-spinner-size);\n  margin-top: calc(-1 * (var(--cx-spinner-size) / 2));\n  margin-inline-start: calc(-1 * (var(--cx-spinner-size) / 2));\n  border-radius: 50%;\n  border: 2px solid var(--cx-color-light);\n  border-top-color: var(--cx-color-primary);\n  animation: spin 0.6s linear infinite;\n  z-index: 1;\n\n  // add smooth transition\n  opacity: var(--cx-opacity, 0);\n  transition: all var(--cx-transition-duration, 0.6s);\n\n  // We prevent pointer events on this pseudo class to avoid any direct mouse\n  // interaction. Mouse interaction with the spinner class affects developer\n  // experience badly, as you can't inspect the actual elements. Also, users\n  // would be blocked to use the context menu to open a link or image in a\n  // new tab.\n  pointer-events: none;\n}\n\n%overlay {\n  content: '';\n  position: absolute;\n  z-index: 1;\n  width: 100%;\n  height: 100%;\n  margin: calc(-1 * var(--cx-margin, 0.5vw));\n\n  // we need a transparent color since the opacity\n  // is already used for animation purposes\n  background-color: hsla(60, 1%, 21%, 0.7);\n\n  opacity: var(--cx-opacity, 0);\n  transition: all var(--cx-transition-duration, 0.6s);\n}\n","/**\n  * We use the hamburgers from https://github.com/jonsuh/hamburgers\n  * In order to allow for custom hamburgers, we use the `cx-hamburger`\n  * selector. This selector extends the hamburger selectors provided \n  * by the lib. In order to easily switch the hamburger type, we introduce the \n  * `$hamburger-type`, that can be set to the types provided by the library. \n  */\n$cx-hamburger-type: squeeze !default;\n\n$hamburger-types: ($cx-hamburger-type);\n\n$hamburger-layer-color: theme-color('inverse') !default;\n$hamburger-layer-width: 26px !default;\n$hamburger-layer-height: 3px !default;\n$hamburger-layer-spacing: 4px !default;\n$hamburger-layer-border-radius: 0 !default;\n$hamburger-padding-y: 10px !default;\n@import '~hamburgers/_sass/hamburgers/hamburgers';\n\n.cx-hamburger {\n  @extend .hamburger, .hamburger--#{$cx-hamburger-type} !optional;\n}\n","@charset \"UTF-8\";\n/*!\n * Hamburgers\n * @description Tasty CSS-animated hamburgers\n * @author Jonathan Suh @jonsuh\n * @site https://jonsuh.com/hamburgers\n * @link https://github.com/jonsuh/hamburgers\n */\n\n// Settings\n// ==================================================\n$hamburger-padding-x           : 15px !default;\n$hamburger-padding-y           : 15px !default;\n$hamburger-layer-width         : 40px !default;\n$hamburger-layer-height        : 4px !default;\n$hamburger-layer-spacing       : 6px !default;\n$hamburger-layer-color         : #000 !default;\n$hamburger-layer-border-radius : 4px !default;\n$hamburger-hover-opacity       : 0.7 !default;\n$hamburger-active-layer-color  : $hamburger-layer-color !default;\n$hamburger-active-hover-opacity: $hamburger-hover-opacity !default;\n\n// To use CSS filters as the hover effect instead of opacity,\n// set $hamburger-hover-use-filter as true and\n// change the value of $hamburger-hover-filter accordingly.\n$hamburger-hover-use-filter   : false !default;\n$hamburger-hover-filter       : opacity(50%) !default;\n$hamburger-active-hover-filter: $hamburger-hover-filter !default;\n\n// Types (Remove or comment out what you don’t need)\n// ==================================================\n$hamburger-types: (\n  3dx,\n  3dx-r,\n  3dy,\n  3dy-r,\n  3dxy,\n  3dxy-r,\n  arrow,\n  arrow-r,\n  arrowalt,\n  arrowalt-r,\n  arrowturn,\n  arrowturn-r,\n  boring,\n  collapse,\n  collapse-r,\n  elastic,\n  elastic-r,\n  emphatic,\n  emphatic-r,\n  minus,\n  slider,\n  slider-r,\n  spin,\n  spin-r,\n  spring,\n  spring-r,\n  stand,\n  stand-r,\n  squeeze,\n  vortex,\n  vortex-r\n) !default;\n\n// Base Hamburger (We need this)\n// ==================================================\n@import \"base\";\n\n// Hamburger types\n// ==================================================\n@import \"types/3dx\";\n@import \"types/3dx-r\";\n@import \"types/3dy\";\n@import \"types/3dy-r\";\n@import \"types/3dxy\";\n@import \"types/3dxy-r\";\n@import \"types/arrow\";\n@import \"types/arrow-r\";\n@import \"types/arrowalt\";\n@import \"types/arrowalt-r\";\n@import \"types/arrowturn\";\n@import \"types/arrowturn-r\";\n@import \"types/boring\";\n@import \"types/collapse\";\n@import \"types/collapse-r\";\n@import \"types/elastic\";\n@import \"types/elastic-r\";\n@import \"types/emphatic\";\n@import \"types/emphatic-r\";\n@import \"types/minus\";\n@import \"types/slider\";\n@import \"types/slider-r\";\n@import \"types/spin\";\n@import \"types/spin-r\";\n@import \"types/spring\";\n@import \"types/spring-r\";\n@import \"types/stand\";\n@import \"types/stand-r\";\n@import \"types/squeeze\";\n@import \"types/vortex\";\n@import \"types/vortex-r\";\n\n// ==================================================\n// Cooking up additional types:\n//\n// The Sass for each hamburger type should be nested\n// inside an @if directive to check whether or not\n// it exists in $hamburger-types so only the CSS for\n// included types are generated.\n//\n// e.g. hamburgers/types/_new-type.scss\n//\n// @if index($hamburger-types, new-type) {\n//   .hamburger--new-type {\n//     ...\n//   }\n// }\n","// Hamburger\n// ==================================================\n.hamburger {\n  padding: $hamburger-padding-y $hamburger-padding-x;\n  display: inline-block;\n  cursor: pointer;\n\n  transition-property: opacity, filter;\n  transition-duration: 0.15s;\n  transition-timing-function: linear;\n\n  // Normalize (<button>)\n  font: inherit;\n  color: inherit;\n  text-transform: none;\n  background-color: transparent;\n  border: 0;\n  margin: 0;\n  overflow: visible;\n\n  &:hover {\n    @if $hamburger-hover-use-filter == true {\n      filter: $hamburger-hover-filter;\n    }\n    @else {\n      opacity: $hamburger-hover-opacity;\n    }\n  }\n\n  &.is-active {\n    &:hover {\n      @if $hamburger-hover-use-filter == true {\n        filter: $hamburger-active-hover-filter;\n      }\n      @else {\n        opacity: $hamburger-active-hover-opacity;\n      }\n    }\n\n    .hamburger-inner,\n    .hamburger-inner::before,\n    .hamburger-inner::after {\n      background-color: $hamburger-active-layer-color;\n    }\n  }\n}\n\n.hamburger-box {\n  width: $hamburger-layer-width;\n  height: $hamburger-layer-height * 3 + $hamburger-layer-spacing * 2;\n  display: inline-block;\n  position: relative;\n}\n\n.hamburger-inner {\n  display: block;\n  top: 50%;\n  margin-top: $hamburger-layer-height / -2;\n\n  &,\n  &::before,\n  &::after {\n    width: $hamburger-layer-width;\n    height: $hamburger-layer-height;\n    background-color: $hamburger-layer-color;\n    border-radius: $hamburger-layer-border-radius;\n    position: absolute;\n    transition-property: transform;\n    transition-duration: 0.15s;\n    transition-timing-function: ease;\n  }\n\n  &::before,\n  &::after {\n    content: \"\";\n    display: block;\n  }\n\n  &::before {\n    top: ($hamburger-layer-spacing + $hamburger-layer-height) * -1;\n  }\n\n  &::after {\n    bottom: ($hamburger-layer-spacing + $hamburger-layer-height) * -1;\n  }\n}\n","@if index($hamburger-types, squeeze) {\n  /*\n   * Squeeze\n   */\n  .hamburger--squeeze {\n    .hamburger-inner {\n      transition-duration: 0.075s;\n      transition-timing-function: cubic-bezier(0.55, 0.055, 0.675, 0.19);\n\n      &::before {\n        transition: top 0.075s 0.12s ease,\n                    opacity 0.075s ease;\n      }\n\n      &::after {\n        transition: bottom 0.075s 0.12s ease,\n                    transform 0.075s cubic-bezier(0.55, 0.055, 0.675, 0.19);\n      }\n    }\n\n    &.is-active {\n      .hamburger-inner {\n        transform: rotate(45deg);\n        transition-delay: 0.12s;\n        transition-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);\n\n        &::before {\n          top: 0;\n          opacity: 0;\n          transition: top 0.075s ease,\n                      opacity 0.075s 0.12s ease;\n        }\n\n        &::after {\n          bottom: 0;\n          transform: rotate(-90deg);\n          transition: bottom 0.075s ease,\n                      transform 0.075s 0.12s cubic-bezier(0.215, 0.61, 0.355, 1);\n        }\n      }\n    }\n  }\n}\n","%cx-base-dialog {\n  position: fixed;\n  z-index: 1050;\n  top: 0;\n  left: 0;\n  height: 100%;\n  width: 100%;\n  outline: 0;\n}\n\n.cx-sidebar-start {\n  @extend %cx-base-dialog;\n  display: flex;\n  justify-content: flex-start;\n}\n\n.cx-sidebar-end {\n  @extend %cx-base-dialog;\n  display: flex;\n  justify-content: flex-end;\n}\n\n.cx-dialog-popover {\n  @extend %cx-base-dialog;\n}\n\n.cx-dialog-popover-center {\n  @extend %cx-base-dialog;\n  display: grid;\n  place-items: center;\n}\n\n.cx-dialog-popover-center-backdrop {\n  @extend .cx-dialog-popover-center;\n  background-color: rgba(0, 0, 0, 0.5);\n}\n","@import 'theme';\n\n:root {\n  @each $color, $value in $theme-colors {\n    --cx-color-#{$color}: #{$value};\n  }\n  --cx-page-width-max: 1140px;\n\n  --cx-font-weight-light: 300;\n  --cx-font-weight-normal: 400;\n  --cx-font-weight-semi: 600;\n  --cx-font-weight-bold: 700;\n\n  --cx-direction: #{$direction};\n\n  --cx-transition-duration: 0.5s;\n  --cx-animation-duration: 1s;\n\n  --cx-border-radius: 1em;\n  --cx-buttons-border-radius: 0.25rem;\n\n  --cx-border-style: solid;\n\n  body {\n    text-align: start;\n  }\n}\n","/**\n * CSS rules to control above-the-page loading and defer loading \n * of additional DOM.\n */\n\n:root {\n  /** \n     * The transition delay for below-the-fold (btf) elements, before they \n     * revert to their original real esate.\n     */\n  --btf-delay: 300ms;\n\n  /** \n     * The real esate of pending elements below-the-fold (btf) is \n     * influenced to ensure these elements will not intersect the \n     * view port. \n     */\n  --btf-min-height: 100vh;\n  --btf-margin-top: 100vh;\n}\n\ncx-page-layout {\n  cx-page-slot {\n    transition: margin-top 0s, min-height 0s;\n    // we delay the minimal real estate for pending slots\n    transition-delay: var(--btf-delay);\n\n    min-height: initial;\n    margin-top: initial;\n    &.cx-pending {\n      // we need a minimum height for pending items, as the slot would\n      // otherwise never intersect in the IntersectionObserver.\n      min-height: 1px;\n    }\n  }\n\n  /** \n     * Select all pending page slots after the pending (!) page-fold, and \n     * move them below the fold with a margin-top.\n     */\n  cx-page-slot.cx-pending.page-fold ~ cx-page-slot.cx-pending {\n    margin-top: var(--btf-margin-top);\n  }\n\n  /** \n     * Select all pending page slots after the page-fold, and add\n     * a minimum height so they would not come in the view port all \n     * at once.\n     */\n  cx-page-slot.page-fold ~ cx-page-slot.cx-pending {\n    min-height: var(--btf-min-height);\n  }\n}\n","/* Loading Animation */\n@keyframes loading {\n  100% {\n    transform: translateX(100%);\n  }\n}\n\n:root {\n  --cx-color-ghost: #f1f1f1;\n  --cx-color-ghost-animation: rgba(255, 255, 255, 0.2);\n  --cx-ghost-radius: 5px;\n  --cx-ghost-margin: 10px;\n}\n\n.ghost {\n  position: relative;\n\n  color: var(--cx-color-ghost);\n\n  &:before {\n    display: block;\n    content: '';\n    position: absolute;\n    z-index: 1;\n    width: 100%;\n    height: 100%;\n    transform: translateX(-100%);\n\n    background: linear-gradient(\n      90deg,\n      transparent,\n      var(--cx-color-ghost-animation),\n      transparent\n    );\n\n    /* Adding animation */\n    animation: loading 0.9s infinite;\n  }\n}\n","cx-page-layout {\n  display: block;\n  width: 100%;\n\n  > header {\n    background-color: var(--cx-color-background);\n    text-align: center;\n    padding-bottom: 25px;\n    padding-top: 25px;\n  }\n\n  cx-page-slot {\n    display: flex;\n    flex-wrap: wrap;\n    justify-content: center;\n    flex: 1 1 var(--cx-flex-basis, 100%);\n    > * {\n      flex: 1 1 var(--cx-flex-basis, 100%);\n    }\n  }\n\n  &.footer {\n    background-color: var(--cx-background-color, var(--cx-color-dark));\n    color: var(--cx-color, var(--cx-color-inverse));\n  }\n}\n\ncx-page-slot {\n  // smartedit requires every slot to be rendered in block mode\n  // most likely it cannot make an overlay without some minimal sizingn\n  display: block;\n}\n\ncx-paragraph {\n  display: block;\n}\n\ncx-category-navigation {\n  display: block;\n}\n","%LandingPage2Template {\n  display: flex;\n  flex-wrap: wrap;\n  max-width: 1700px;\n  margin: auto;\n\n  cx-page-slot {\n    cx-banner {\n      cx-media {\n        img {\n          display: flex;\n          height: auto;\n        }\n      }\n    }\n\n    &.Section1 {\n      max-width: var(--cx-page-width-max);\n      margin: auto;\n      margin-top: 30px;\n\n      @include media-breakpoint-down(md) {\n        margin-top: 0;\n      }\n    }\n\n    &.Section2A {\n      margin-inline-start: auto;\n    }\n\n    &.Section2B {\n      margin-inline-end: auto;\n    }\n\n    &.Section2A,\n    &.Section2B {\n      --cx-flex-basis: 50%;\n      max-width: 570px;\n      @include media-breakpoint-down(md) {\n        --cx-flex-basis: 100%;\n        max-width: 100%;\n        > * {\n          --cx-flex-basis: 50%;\n        }\n      }\n\n      @include media-breakpoint-only(xs) {\n        > * {\n          --cx-flex-basis: 100%;\n        }\n      }\n    }\n\n    &.Section3 {\n      max-width: var(--cx-page-width-max);\n      margin: auto;\n    }\n\n    &.Section4 {\n      color: var(--cx-color, var(--cx-color-light));\n      flex-wrap: nowrap;\n\n      @include media-breakpoint-down(md) {\n        flex-wrap: wrap;\n        > * {\n          --cx-flex-basis: 50%;\n          border-bottom: solid 1px currentColor;\n          border-inline-end: solid 1px currentColor;\n        }\n      }\n\n      @include media-breakpoint-only(xs) {\n        > * {\n          --cx-flex-basis: 100%;\n        }\n      }\n    }\n  }\n}\n","%ContentPage1Template {\n  // @extend %pageBody !optional;\n\n  max-width: var(--cx-page-width-max);\n  padding-top: 2rem;\n  padding-inline-end: 1.5rem;\n  padding-bottom: 0;\n  padding-inline-start: 1.5rem;\n  margin: auto;\n  cx-page-slot {\n 