breakpoint-up(lg) {\n      .wrapper {\n        // we add a z-index since some of the elements on the page have (surprisingly) a z-index rule\n        // we cannot make the z-index too high as it would conflict with the searchbox flyout.\n        z-index: 20;\n      }\n      > nav > .wrapper {\n        padding: 15px 20px;\n        background: #fff;\n      }\n    }\n  }\n}\n\n%nav-back-button {\n  &.flyout {\n    .back {\n      display: none;\n    }\n    @include media-breakpoint-down(md) {\n      .back {\n        display: initial;\n        h5 {\n          text-transform: capitalize;\n          justify-content: flex-start;\n          cx-icon {\n            padding-inline-end: 10px;\n          }\n        }\n      }\n      &:not(.is-open) {\n        .back {\n          display: none;\n        }\n      }\n    }\n  }\n}\n\n%nav-heading {\n  nav {\n    &:focus {\n      color: var(--cx-g-color-primary);\n    }\n  }\n\n  h5 {\n    margin: 0;\n  }\n\n  &.flyout {\n    h5,\n    cx-generic-link {\n      display: flex;\n      align-items: center;\n      white-space: nowrap;\n\n      color: currentColor;\n\n      &:hover {\n        color: var(--cx-color-primary);\n      }\n\n      &:focus {\n        z-index: 1;\n        position: relative;\n      }\n\n      a {\n        display: block;\n        width: 100%;\n        &:focus {\n          z-index: 1;\n          position: relative;\n        }\n      }\n    }\n\n    @include media-breakpoint-down(md) {\n      // make all first level hedings uppercase in mobile view\n      > nav {\n        > h5,\n        > cx-generic-link {\n          text-transform: uppercase;\n          font-weight: 600;\n        }\n      }\n      h5,\n      cx-generic-link {\n        border-bottom: 1px solid var(--cx-color-light);\n        display: flex;\n        justify-content: space-between;\n\n        cursor: pointer;\n      }\n\n      h5,\n      cx-generic-link a {\n        padding: 1rem;\n      }\n    }\n\n    @include media-breakpoint-up(lg) {\n      > nav {\n        cursor: pointer;\n        // top level headings\n        > h5 {\n          margin-top: 3px;\n          padding-top: 20px;\n          padding-inline-end: 15px;\n          padding-bottom: 25px;\n          padding-inline-start: 0;\n        }\n        // first level headings will be bold in desktop\n        nav > h5 {\n          @include type('5');\n          cursor: default;\n\n          &:hover {\n            color: currentColor;\n          }\n        }\n      }\n    }\n  }\n  cx-generic-link.all {\n    text-decoration: underline;\n  }\n}\n\n@include media-breakpoint-up(lg) {\n  cx-navigation-ui > nav > cx-generic-link > a {\n    padding-top: 20px;\n    padding-inline-end: 15px;\n    padding-bottom: 22px;\n    padding-inline-start: 0;\n  }\n  nav > div > cx-generic-link {\n    padding: 10px 0;\n  }\n  div.childs > nav > cx-generic-link > a {\n    padding: 5px 0;\n  }\n}\n\n%nav-icon {\n  cx-icon {\n    &:before {\n      font-size: 1.5rem;\n    }\n  }\n  @include media-breakpoint-up(lg) {\n    cx-icon {\n      padding-inline-start: 7px;\n      &:before {\n        font-size: 1rem;\n      }\n    }\n  }\n}\n\n%nav-childs {\n  &.flyout {\n    // the last level of navigations is tyically displayed in a column\n    .childs[depth='1'] {\n      flex-direction: column;\n    }\n    .childs {\n      @include media-breakpoint-up(lg) {\n        .childs {\n          padding: 0;\n        }\n      }\n    }\n  }\n}\n\n%nav-links {\n  a {\n    color: currentColor;\n  }\n  &.flyout {\n    a {\n      display: block;\n      text-decoration: none;\n      white-space: nowrap;\n    }\n  }\n}\n\n%nav-wrap {\n  @for $column from 1 through 10 {\n    &.flyout .childs[columns='#{$column}'] {\n      display: block;\n\n      @include media-breakpoint-up(md) {\n        column-count: $column;\n      }\n      @include media-breakpoint-down(sm) {\n        column-count: ceil($column / 3);\n      }\n    }\n  }\n}\n\n%cx-navigation-ui {\n  display: flex;\n\n  @extend %nav-heading;\n  @extend %nav-icon;\n  @extend %nav-wrapper;\n  @extend %nav-childs;\n  @extend %nav-links;\n  @extend %nav-back-button;\n  @extend %nav-wrap;\n\n  nav {\n    outline: none;\n  }\n\n  &.flyout {\n    @include media-breakpoint-down(md) {\n      flex-direction: column;\n      color: #000;\n    }\n\n    @include media-breakpoint-down(md) {\n      &.is-open {\n        > nav {\n          display: none;\n          cx-generic-link.all {\n            display: none;\n          }\n        }\n        nav.is-open {\n          display: initial;\n          cx-generic-link.all {\n            display: initial;\n          }\n          > .wrapper {\n            height: auto;\n          }\n        }\n\n        nav.is-opened {\n          display: initial;\n          > .wrapper > .childs > nav {\n            display: none;\n            &.is-open {\n              display: initial;\n            }\n          }\n          > h5 {\n            display: none;\n          }\n          > .wrapper {\n            height: auto;\n          }\n        }\n      }\n    }\n\n    @include media-breakpoint-up(lg) {\n      .childs[depth='2'] {\n        > nav {\n          &:not(:only-child):not(:last-child) {\n            padding-inline-end: 20px;\n          }\n        }\n      }\n      // Show dropdowns via hover when no nav is focused\n      > nav {\n        > .wrapper {\n          position: absolute;\n          margin-inline-start: -10px;\n          border: 1px solid #ccc;\n          @include hideWrapperDesktop();\n        }\n\n        /* we only treat the first wrapper as a collapsible container */\n        &:hover {\n          > .wrapper {\n            @include showWrapperDesktop();\n          }\n        }\n      }\n\n      // Show dropdowns via focus only once a nav is clicked\n      &:focus-within {\n        > nav {\n          &:focus,\n          &.is-open:focus-within {\n            > .wrapper {\n              @include showWrapperDesktop();\n            }\n          }\n        }\n      }\n\n      .childs {\n        display: flex;\n      }\n\n      .wrapper cx-icon {\n        display: none;\n      }\n\n      > nav {\n        > .wrapper {\n          transition: opacity 0.4s;\n        }\n      }\n    }\n  }\n}\n","%cx-category-navigation {\n  // all ategory headings in desktop have uppercase text, as\n  // well as the root category links\n  h5,\n  cx-navigation-ui > nav > cx-generic-link {\n    text-transform: uppercase;\n    font-weight: 600;\n  }\n\n  // we hide the categories in flyout mode on mobile\n  cx-navigation-ui.flyout {\n    @include media-breakpoint-down(md) {\n      &.is-open {\n        nav.is-open {\n          > h5 {\n            display: none;\n          }\n        }\n      }\n    }\n  }\n}\n","%cx-footer-navigation {\n  a {\n    font-size: var(--cx-font-small, 0.8rem);\n    &:hover {\n      color: var(--cx-color-inverse);\n      text-decoration: underline;\n    }\n  }\n\n  cx-navigation-ui {\n    // first level heading / links\n    h5 {\n      text-transform: uppercase;\n      @include type('5');\n      margin-bottom: 20px;\n    }\n  }\n\n  cx-navigation-ui {\n    justify-content: center;\n    @include media-breakpoint-down(sm) {\n      flex-direction: column;\n    }\n\n    > nav {\n      margin: 3vw;\n    }\n  }\n}\n","%cx-media {\n  position: relative;\n  display: block;\n\n  img {\n    max-width: 100%;\n    max-height: 100%;\n    transform: scale(1);\n    opacity: 1;\n    transition: all 0.6s;\n  }\n\n  &.is-missing {\n    @extend %cx-media-missing !optional;\n    height: 100%;\n  }\n}\n\n%cx-media-missing {\n  background: no-repeat top center\n    url('data:image/jpg;base64,');\n  background-size: contain;\n}\n","$useTabsOnPDP: false !default;\n\n%pdpTabs {\n  // On small devices we hide the toggled items\n  // in order to allow for a more condensed layout\n  @include media-breakpoint-down(sm) {\n    > div.active.toggled {\n      display: none;\n    }\n    > button.active.toggled {\n      color: initial;\n      &:before {\n        content: '+';\n      }\n      &:after {\n        width: 0;\n        opacity: var(--cx-opacity, 0);\n      }\n    }\n  }\n\n  > div {\n    background: var(--cx-color-background);\n    &:not(.active) {\n      display: none;\n    }\n    .container > h2 {\n      display: none;\n    }\n  }\n  > button {\n    @include button-reset();\n    margin-bottom: 0;\n    &.active,\n    &:hover {\n      color: var(--cx-color-primary);\n    }\n    &:focus {\n      z-index: 1;\n    }\n    cursor: pointer;\n    @include media-breakpoint-up(md) {\n      &.active {\n        cursor: initial;\n      }\n    }\n\n    // border effect\n    &:after {\n      content: '';\n      display: block;\n      margin-top: 15px;\n      margin-inline-end: auto;\n      margin-bottom: auto;\n      margin-inline-start: auto;\n      height: 5px;\n      background: var(--cx-color-primary);\n\n      // the tab hover effect uses a border in the :after pseudo\n      // that is animated from 0% to 100% width\n      width: 0;\n      opacity: 0;\n      transition: all 0.6s;\n    }\n\n    &.active:after,\n    &:hover:after {\n      width: 100%;\n    }\n    &.active:after,\n    &.active:hover:after {\n      opacity: 1;\n    }\n    &:not(.active):hover:after {\n      opacity: 0.5;\n    }\n  }\n\n  @include media-breakpoint-down(sm) {\n    > button {\n      border-top: 1px var(--cx-color-light) solid;\n      font-size: 18px;\n      padding-top: 15px;\n      padding-inline-end: 0;\n      padding-bottom: 0;\n      padding-inline-start: 4%;\n      width: 100%;\n      text-align: start;\n      height: 63px;\n\n      &:before {\n        content: '+';\n        margin: 0px 15px;\n        float: right;\n        font-size: 33px;\n        bottom: 12px;\n        position: relative;\n        color: var(--cx-color-text);\n      }\n      &:after {\n        margin-top: 15px;\n        margin-inline-end: auto;\n        margin-bottom: auto;\n        margin-inline-start: -4%;\n      }\n      &.active {\n        &:before {\n          content: '–';\n        }\n      }\n      &.active:after,\n      &:hover:after {\n        width: 104%;\n      }\n    }\n\n    > div {\n      padding: 15px 5px;\n    }\n  }\n\n  // in order to align the tabs to the left\n  // we simulate the container size\n  @include media-breakpoint-only(md) {\n    grid-template-columns: auto 10px repeat(4, 180px) 0px auto;\n  }\n  @include media-breakpoint-only(lg) {\n    grid-template-columns: auto 10px repeat(4, 180px) 272px auto;\n  }\n  @include media-breakpoint-only(xl) {\n    grid-template-columns: auto 10px repeat(4, 180px) 420px auto;\n  }\n\n  @include media-breakpoint-up(md) {\n    display: grid;\n\n    > button {\n      order: 0;\n      transition: all 0.6s;\n      font-size: 1.2rem;\n      text-align: center;\n      margin: 0;\n\n      &:first-of-type {\n        grid-column: 3;\n      }\n    }\n\n    // the div elements contain the content and will be rendered after the tabs\n    > div {\n      order: 2;\n      grid-column-start: 1;\n      grid-column-end: -1;\n      padding: 50px 0;\n      border-top: 1px var(--cx-color-light) solid;\n    }\n  }\n}\n\n%pdpFlat {\n  margin: auto;\n\n  @include media-breakpoint-down(xl) {\n    max-width: var(--cx-page-width-max);\n  }\n  // make a small header with links\n  display: grid;\n  grid-template-columns: repeat(4, auto) auto;\n  justify-content: start;\n  > button {\n    @include button-reset();\n    order: 2;\n    color: var(--cx-color-primary);\n    font-weight: normal;\n    font-size: 0.9em;\n    cursor: pointer;\n    margin: 10px 15px;\n    &:hover {\n      text-decoration: underline;\n    }\n    &:focus {\n      z-index: 1;\n    }\n  }\n\n  > div {\n    order: 4;\n    grid-column: 1 / span 5;\n  }\n}\n\n%cx-tab-paragraph-container {\n  @if $useTabsOnPDP == true {\n    @extend %pdpTabs !optional;\n  } @else {\n    @extend %pdpFlat !optional;\n  }\n}\n","@mixin button-reset {\n  margin: 0;\n  padding: 0;\n  // font-smoothing: antialiased; left here for the future\n  appearance: none;\n  outline: 0;\n  border: 0;\n  display: inline-block;\n  text-decoration: none;\n  cursor: pointer;\n  user-select: none;\n  vertical-align: middle;\n  white-space: nowrap;\n  background-color: transparent;\n}\n","%cx-global-message {\n  position: sticky;\n  display: block;\n  top: 0;\n  z-index: 2;\n}\n","@import '../../../theme';\n\n/**\n * To control visual effects while searching the searchbox requries\n * some CSS rules to the outer elements.\n */\n%cx-searchbox__body {\n  // when there are results and the searchbox is active\n  // we display the searchbox\n  &.searchbox-is-active {\n    &.has-searchbox-results {\n      cx-searchbox {\n        .results {\n          display: block;\n        }\n      }\n\n      // the searchbox requires an overlay over the page (except for the header)\n      // we do this with an overlay under the header, on top of the rest of the page\n      header {\n        position: relative;\n        z-index: 100;\n      }\n      cx-storefront {\n        &:before {\n          content: '';\n          position: fixed;\n          left: 0;\n          top: 0;\n          width: 100%;\n          height: 100%;\n          z-index: 10;\n\n          background: rgba(0, 0, 0, 0.5);\n        }\n      }\n    }\n  }\n\n  &:not(.searchbox-is-active) {\n    @include media-breakpoint-down(sm) {\n      // hide the input on mobile when there's no interaction with searchbox\n      cx-searchbox {\n        input {\n          // we cannot use display:none, visible:hidden or opacity: 0\n          // as this will no longer emit a focus event to the controller logic\n          width: 0;\n          padding: 0;\n        }\n        cx-icon.reset {\n          display: none;\n        }\n      }\n    }\n  }\n}\n\n%cx-searchbox {\n  @include media-breakpoint-up(md) {\n    // we position the parent relative to ensure the result panel\n    // is aligned to the left of searchbox\n    position: relative;\n    z-index: 0;\n  }\n\n  z-index: 10;\n  display: block;\n\n  > * {\n    z-index: 20;\n    @include media-breakpoint-up(md) {\n      background-color: var(--cx-color-secondary);\n      position: relative;\n    }\n  }\n\n  a,\n  .message {\n    padding: 6px 16px;\n    color: currentColor;\n    user-select: none;\n  }\n\n  label {\n    display: flex;\n    align-content: stretch;\n    margin: 0;\n    padding-top: 6px;\n    padding-inline-end: 6px;\n    padding-bottom: 6px;\n    padding-inline-start: 10px;\n    @include media-breakpoint-up(md) {\n      width: 27vw;\n      min-width: 300px;\n      max-width: 550px;\n      // hide search icon when the input is dirty\n      &.dirty cx-icon.search {\n        display: none;\n      }\n    }\n\n    input {\n      background: none;\n      border: none;\n      outline: none;\n\n      display: block;\n\n      @include media-breakpoint-down(sm) {\n        position: absolute;\n        left: 0;\n        top: 57px;\n        width: 100%;\n        background-color: var(--cx-color-secondary);\n        padding: 6px 16px;\n        height: 48px;\n        border-bottom: 1px solid var(--cx-color-light);\n      }\n\n      flex-basis: 100%;\n      height: 35px;\n      color: currentColor;\n      z-index: 20;\n\n      @include placeholder {\n        color: currentColor;\n      }\n    }\n\n    // hide reset icon when the input is empty\n    &:not(.dirty) cx-icon.reset {\n      display: none;\n    }\n\n    cx-icon {\n      color: currentColor;\n      flex-basis: 48px;\n      text-align: center;\n      cursor: pointer;\n      &.reset {\n        &:before {\n          font-size: 1.4rem;\n        }\n        @include media-breakpoint-down(sm) {\n          position: relative;\n          left: 74px;\n          z-index: 20;\n          top: 52px;\n          margin-top: 0;\n        }\n      }\n    }\n  }\n\n  .results {\n    // hide the result by default\n    display: none;\n    position: absolute;\n    left: 0;\n    width: 100%;\n    color: #000;\n    background-color: #fff;\n    font-size: var(--cx-font-small, 1rem);\n\n    @include media-breakpoint-down(sm) {\n      top: 105px;\n      z-index: 10;\n    }\n\n    a {\n      text-decoration: none;\n      cursor: pointer;\n\n      font-weight: 800;\n\n      .highlight,\n      .search-results-highlight {\n        font-weight: normal;\n        font-style: normal;\n      }\n\n      &:hover,\n      &:focus {\n        background: var(--cx-color-light);\n        color: currentColor;\n      }\n    }\n    .suggestions {\n      display: flex;\n      flex-wrap: wrap;\n      a {\n        flex: 100%;\n        line-height: 2rem;\n      }\n    }\n    .products {\n      @include media-breakpoint-down(sm) {\n        display: none;\n      }\n\n      a {\n        display: grid;\n        grid-column-gap: 16px;\n\n        &.has-media {\n          // create a grid layout in case we show an image\n          grid-template-columns: 50px 1fr;\n        }\n\n        border-top: solid 1px var(--cx-color-light);\n\n        cx-media {\n          grid-row: 1 / 3;\n        }\n\n        h4.name {\n          text-overflow: ellipsis;\n\n          font-size: inherit;\n          font-weight: inherit;\n\n          /* Required for text-overflow to do anything */\n          white-space: nowrap;\n          overflow: hidden;\n        }\n        .price {\n          font-weight: normal;\n        }\n      }\n    }\n  }\n}\n","@mixin placeholder {\n  &::-webkit-input-placeholder {\n    @content;\n  }\n  &::-moz-placeholder {\n    @content;\n  }\n  &:-moz-placeholder {\n    @content;\n  }\n  &:-ms-input-placeholder {\n    @content;\n  }\n}\n","$intialExpandedFacets: 0 !default;\n$intialExpandedFacetsLarge: 3 !default;\n\n%facet-heading {\n  display: flex;\n  align-items: center;\n  justify-content: space-between;\n\n  margin: 0.4rem 0;\n  padding: 0.4rem;\n  width: 100%;\n\n  border-bottom: 1px solid var(--cx-color-light);\n\n  cursor: pointer;\n\n  font-weight: var(--cx-font-weight-semi);\n\n  &:hover,\n  &:focus {\n    color: var(--cx-color-primary);\n  }\n\n  cx-icon {\n    font-size: 1rem;\n  }\n\n  @include media-breakpoint-down(md) {\n    cx-icon {\n      font-size: 1.3rem;\n    }\n  }\n}\n\n%cx-facet {\n  button.heading {\n    @extend %facet-heading !optional;\n  }\n\n  display: block;\n\n  @include media-breakpoint-down(md) {\n    &:nth-of-type(-n + #{$intialExpandedFacets}) {\n      &.collapsed {\n        .collapse-icon,\n        a,\n        button:not(.heading) {\n          display: none;\n        }\n      }\n      &:not(.collapsed) .expand-icon {\n        display: none;\n      }\n    }\n    &:nth-of-type(n + #{$intialExpandedFacets + 1}) {\n      &:not(.expanded) {\n        .collapse-icon,\n        a,\n        button:not(.heading) {\n          display: none;\n        }\n      }\n      &.expanded .expand-icon {\n        display: none;\n      }\n    }\n  }\n\n  @include media-breakpoint-up(lg) {\n    // first number of facets\n    &:nth-of-type(-n + #{$intialExpandedFacetsLarge}) {\n      &.collapsed {\n        .collapse-icon,\n        a,\n        button:not(.heading) {\n          display: none;\n        }\n      }\n      &:not(.collapsed) .expand-icon {\n        display: none;\n      }\n    }\n\n    // other facets lower in the list\n    &:nth-of-type(n + #{$intialExpandedFacetsLarge + 1}) {\n      &:not(.expanded) {\n        .collapse-icon,\n        a,\n        button:not(.heading) {\n          display: none;\n        }\n      }\n      &.expanded .expand-icon {\n        display: none;\n      }\n    }\n  }\n\n  a,\n  button {\n    display: flex;\n    align-items: baseline;\n    padding: 0.4rem;\n\n    cursor: pointer;\n\n    color: var(--cx-color-text);\n\n    &.value {\n      &:hover {\n        text-decoration: none;\n        color: var(--cx-color-primary);\n      }\n\n      // In mobile view, we keep the facet values on a single line to avoid a lot\n      // of space being used. Multi-line facet will not flow over.\n      @include media-breakpoint-down(md) {\n        > span {\n          display: flex;\n          width: calc(100% - 1.8rem);\n        }\n   