de media-breakpoint-up(lg) {\n      h4 {\n        display: none;\n      }\n    }\n  }\n\n  &.dialog {\n    @extend %cx-modal;\n    > .inner {\n      width: 95vw;\n      height: 95%;\n    }\n  }\n}\n","%cx-product-facet-navigation {\n  min-width: 0;\n\n  // hides the filter button in desktop experience\n  @include media-breakpoint-up(lg) {\n    button.dialog-trigger {\n      display: none;\n    }\n  }\n}\n","$spinWhileLoading: false !default;\n$spinWhileWaiting: false !default;\n$overlayWhileLoading: false !default;\n$zoomThumbOnHover: false !default;\n$zoomOutWhileLoading: false !default;\n\n$productThumbSize: 75px !default;\n\n%cx-product-images {\n  display: flex;\n  flex-direction: column;\n\n  height: 100%;\n  > cx-media {\n    height: 100%;\n    width: 100%; // required to support horizontal layouts\n    position: relative;\n\n    img {\n      object-fit: scale-down;\n    }\n  }\n\n  cx-media {\n    outline: none;\n    text-align: center;\n\n    &.is-missing {\n      min-height: 30vh;\n    }\n\n    @if $zoomOutWhileLoading == true {\n      &.is-initialized.is-loading {\n        --cx-zoom: 0.5;\n      }\n    }\n\n    @if $spinWhileLoading == true OR $spinWhileWaiting == true {\n      &:after {\n        @extend %spinner !optional;\n      }\n      &:not(.is-loading):after {\n        animation-play-state: paused;\n      }\n    }\n\n    // the effects run always, but are made visible only when loading or waiting\n    // this is done so we can slowly fadein/out the effect\n    @if $spinWhileLoading == true {\n      &.is-loading:after {\n        --cx-opacity: 1;\n      }\n    }\n    @if $spinWhileWaiting == true {\n      &.is-waiting:after {\n        --cx-opacity: 1;\n      }\n    }\n  }\n\n  .thumbs {\n    cx-media {\n      margin: 0.5vw;\n      padding: 0.5vw;\n\n      cursor: pointer;\n\n      &.is-missing {\n        min-height: 80px;\n      }\n\n      border: 2px solid var(--cx-color-light);\n\n      &.is-active,\n      &:hover {\n        border-color: var(--cx-color-primary);\n      }\n    }\n  }\n}\n","%cx-product-summary {\n  display: flex;\n  flex-direction: column;\n  flex-grow: 0;\n\n  @include media-breakpoint-down(md) {\n    padding-top: 1.5em;\n  }\n\n  @include media-breakpoint-up(lg) {\n    padding-top: 20px;\n    padding-inline-end: 20px;\n    padding-bottom: 0px;\n    padding-inline-start: 20px;\n  }\n\n  .price {\n    display: inline-block;\n    @include type('3');\n    margin: 0 0 11px 0;\n  }\n\n  .quantity {\n    label {\n      @include type('8');\n      margin: 15px 0 10px 0;\n    }\n\n    .info {\n      @include type('7');\n      margin-top: 0;\n      margin-inline-end: 15px;\n      margin-bottom: 0;\n      margin-inline-start: 15px;\n      color: var(--cx-color-secondary);\n    }\n  }\n\n  cx-add-to-cart {\n    margin: 20px 0 10px 0;\n  }\n}\n","%cx-product-attributes {\n  table {\n    width: 100%;\n    margin: 0 0 30px 0;\n    th {\n      h3 {\n        margin: 0 0 18px 0;\n      }\n    }\n    tr {\n      border-color: var(--cx-color-light);\n      border-width: 1px 0 1px;\n      border-style: solid;\n    }\n    td {\n      padding: 12px 0 12px 0;\n      vertical-align: top;\n      width: 50%;\n    }\n    ul {\n      list-style: none;\n      padding: 0 0 0 0;\n    }\n  }\n}\n","$gutterSize: 15px;\n\n%cx-product-reviews {\n  .rating-input {\n    display: none;\n  }\n\n  .header {\n    display: flex;\n    flex-wrap: wrap;\n\n    border-bottom: 1px solid var(--cx-color-light);\n\n    padding: 0 $gutterSize $gutterSize;\n    margin: 0 -#{$gutterSize} 40px -#{$gutterSize};\n\n    button {\n      margin-inline-start: auto;\n    }\n    .rating {\n      flex-basis: 100%;\n    }\n  }\n\n  .review {\n    display: grid;\n    grid-template-columns: auto auto 10vw;\n    grid-template-rows: repeat(3, minmax(10px, auto)) auto;\n    grid-column-gap: 1vw;\n    margin: 4vh 0 2vh;\n\n    // layout\n    cx-star-rating,\n    .title,\n    .text {\n      grid-column: 1 / span 2;\n    }\n    @include media-breakpoint-down(md) {\n      .text {\n        grid-column: 1 / span 3;\n      }\n    }\n    .date {\n      grid-column: 1 / span 1;\n    }\n    .name {\n      grid-column: 1 / span 1;\n    }\n    .text {\n      grid-row: 0;\n    }\n\n    // formatting\n    .title {\n      font-weight: bold;\n    }\n    .text {\n      margin: 0.5vh 0;\n    }\n    .name,\n    .date {\n      text-align: start;\n    }\n  }\n\n  @include media-breakpoint-down(md) {\n    .btn-secondary {\n      margin: 0 0 1rem;\n    }\n  }\n}\n","%cx-product-intro {\n  @include media-breakpoint-up(lg) {\n    padding-top: 20px;\n    padding-inline-end: 20px;\n    padding-bottom: 0px;\n    padding-inline-start: 20px;\n  }\n\n  .code {\n    @include type('7');\n    color: var(--cx-color-secondary);\n    margin: 0 0 20px 0;\n  }\n\n  .rating {\n    display: flex;\n    flex-direction: row;\n    align-items: baseline;\n\n    .count {\n      margin-top: 0;\n      margin-inline-end: 20px;\n      margin-bottom: 0;\n      margin-inline-start: 5px;\n    }\n\n    cx-star-rating {\n      padding: 0 0 15px 0;\n    }\n  }\n}\n","%cx-product-variants {\n  .variant-section {\n    @include media-breakpoint-up(lg) {\n      padding-top: 20px;\n      padding-inline-end: 20px;\n      padding-bottom: 0px;\n      padding-inline-start: 20px;\n    }\n  }\n  .variant-selector {\n    a.size-guide {\n      display: flex;\n      justify-content: flex-end;\n      pointer-events: none;\n    }\n    .style-name {\n      text-transform: capitalize;\n    }\n  }\n  .variant-list {\n    padding: 0;\n\n    li {\n      display: inline-block;\n\n      &.selected-variant {\n        img {\n          border: 2px solid var(--cx-color-primary);\n        }\n      }\n\n      .variant-button {\n        @include button-reset();\n\n        &:focus {\n          @include visible-focus();\n        }\n\n        img {\n          margin: 3px;\n          border: 1px solid #ddd;\n        }\n      }\n    }\n  }\n}\n","%cx-stock-notification {\n  .stock-notification-notes {\n    p {\n      margin: 0 0 10px 0;\n      a {\n        @include type('6');\n        color: var(--cx-color-dark);\n        &:hover {\n          color: var(--cx-color-primary);\n        }\n      }\n    }\n  }\n}\n","%cx-stock-notification-dialog {\n  .cx-dialog-title {\n    @include type('3');\n  }\n\n  .channels {\n    span {\n      @include type('6');\n    }\n  }\n\n  .container p a {\n    @include type('6');\n    color: var(--cx-color-dark);\n    &:hover {\n      color: var(--cx-color-primary);\n    }\n  }\n}\n","%cx-banner-carousel {\n  // let's do a FULL size carousel\n  .carousel-panel {\n    overflow: hidden;\n  }\n  .inline-navigation {\n    position: relative;\n    .carousel-panel {\n      position: relative;\n\n      // force a max-height for the carousel and underlying items\n      &,\n      .item {\n        max-height: 600px;\n      }\n\n      .previous,\n      .next {\n        position: absolute;\n        z-index: 10;\n        height: 100%;\n        width: 5vw;\n        transition: all 0.4s;\n        &:not([disabled]):hover {\n          font-size: 3rem;\n        }\n      }\n      .next {\n        right: 0;\n      }\n    }\n    .indicators {\n      margin-top: -60px;\n      z-index: 1;\n    }\n  }\n\n  cx-banner {\n    a {\n      display: flex;\n      flex-direction: column;\n    }\n  }\n}\n","%cx-carousel {\n  display: flex;\n  flex: 100%;\n  --cx-speed: 0.5;\n  flex-direction: column;\n\n  > h3 {\n    @include type('3');\n    font-weight: bold;\n    text-align: center;\n    margin-top: 2rem;\n    margin-bottom: 1rem;\n    @include media-breakpoint-up(xl) {\n      margin-bottom: 3rem;\n    }\n  }\n\n  .carousel-panel {\n    display: flex;\n    justify-content: space-between;\n\n    // The size of carousel items depends on the number of items per slide.\n    // We generate 10 sizes in case there are a lot of carousel items displayed\n    // on a single slide.\n    @for $i from 1 through 10 {\n      &.size-#{$i} .item {\n        flex: 0 0 calc((100 / #{$i}) * 1%);\n      }\n    }\n\n    .slides {\n      flex: auto;\n      position: relative;\n\n      .slide {\n        transition: 0.6s all;\n        width: 100%;\n        display: flex;\n        flex-wrap: nowrap;\n        justify-content: flex-start;\n\n        &:not(.active) {\n          // we keep the active slide non-absolute, so the height\n          // of the parent is based on the displayed slide\n          position: absolute;\n          opacity: 0;\n          z-index: -1;\n          transition: none;\n        }\n\n        .item {\n          opacity: 0;\n          z-index: -1;\n          &.active {\n            opacity: 1;\n            z-index: 1;\n          }\n          transition: 0.4s all;\n\n          // we add a transition delay so that items are nicely animated in a sequence\n          @for $i from 1 through 4 {\n            &:nth-child(#{$i}) {\n              transition-delay: calc(var(--cx-speed, 1) * #{$i * 0.25s});\n            }\n          }\n        }\n      }\n    }\n  }\n\n  button {\n    &:focus {\n      outline: none;\n    }\n    color: var(--cx-color-light);\n    &:not(:disabled) {\n      cursor: pointer;\n    }\n  }\n\n  .indicators {\n    display: flex;\n    justify-content: center;\n\n    button {\n      border: none;\n      padding: 10px;\n      margin: 0;\n      transition: 0.6s all;\n      background-color: transparent;\n      &[disabled] {\n        color: var(--cx-color-primary);\n      }\n      &:not(:disabled):hover {\n        color: var(--cx-color-secondary);\n      }\n    }\n\n    @include media-breakpoint-only(xs) {\n      display: none;\n    }\n  }\n\n  .previous,\n  .next {\n    background-color: transparent;\n    border: none;\n    font-size: 2rem;\n\n    &:disabled {\n      opacity: 0.5;\n    }\n\n    &:not(:disabled):hover {\n      color: var(--cx-color-primary);\n    }\n  }\n}\n","%cx-product-carousel {\n  display: flex;\n  a {\n    display: flex;\n    flex-direction: column;\n    text-align: center;\n    justify-content: space-between;\n    padding-bottom: 10px;\n    color: var(--cx-color-text);\n    outline: none;\n    h4 {\n      font-size: 0.9rem;\n      height: auto;\n      min-height: 30px;\n      font-weight: bold;\n      margin-top: 5px;\n    }\n    &:hover,\n    &:focus {\n      color: var(--cx-color-primary);\n      text-decoration: underline;\n    }\n    &:focus {\n      position: relative;\n      z-index: 1;\n    }\n  }\n  cx-media {\n    align-self: center;\n    width: 60%;\n\n    &.is-missing {\n      min-height: 255px;\n      align-self: auto;\n      width: auto;\n    }\n  }\n}\n","%cx-product-references {\n  padding-bottom: 50px;\n  a {\n    display: flex;\n    flex-direction: column;\n    text-align: center;\n    justify-content: space-between;\n    padding-bottom: 10px;\n    color: var(--cx-color-text);\n\n    &:focus {\n      color: var(--cx-color-primary);\n      z-index: 1;\n      position: relative;\n    }\n  }\n\n  cx-media {\n    align-self: center;\n    width: 60%;\n\n    &.is-missing {\n      min-height: 200px;\n    }\n  }\n}\n","%cx-star-rating {\n  padding: 15px 0;\n\n  &:not([disabled='true']) {\n    cursor: pointer;\n  }\n\n  .star {\n    font-size: 20px;\n    margin: 0 2.5px;\n\n    @for $i from 1 to 6 {\n      &:nth-child(#{$i}) {\n        background: linear-gradient(\n          calc(var(--star-rating-dir, 1) * 90deg),\n          var(--cx-color-primary) 0%,\n          var(--cx-color-primary) calc((var(--star-fill) - #{$i} + 1) * 100%),\n          var(--cx-color-light) calc((var(--star-fill) - #{$i} + 1) * 100%)\n        );\n        // somehow we cannot move the text related clip and fill color outside this loop.\n        // most likely they cannot come before he definition of the background.\n        background-clip: text;\n        -webkit-background-clip: text;\n        -webkit-text-fill-color: transparent;\n      }\n    }\n  }\n}\n\n// star rating direction is used to fix the lack of start/end\n[dir='rtl'] .star {\n  --star-rating-dir: -1;\n}\n","%cx-card {\n  .cx-card-border {\n    border: 1px solid var(--cx-color-light);\n  }\n\n  .cx-card-container {\n    display: flex;\n  }\n\n  .cx-card-label-container {\n    flex-grow: 2;\n  }\n\n  .cx-card-paragraph {\n    padding-inline-start: 0.7em;\n\n    .cx-card-paragraph-title {\n      padding-top: 0.7em;\n    }\n  }\n\n  .cx-card-fit-to-container {\n    width: 100%;\n    height: 100%;\n    display: flex;\n    flex-direction: column;\n  }\n\n  .cx-card-body {\n    display: flex;\n    flex-direction: column;\n    justify-content: space-between;\n  }\n\n  .cx-card-delete {\n    background-color: var(--cx-color-background);\n  }\n\n  .cx-card-body-delete {\n    padding: 1rem 0 0 0;\n\n    .btn-secondary {\n      @include media-breakpoint-down(sm) {\n        margin: 0 0 1rem;\n      }\n    }\n  }\n\n  .cx-card-delete-msg {\n    color: var(--cx-color-danger);\n    padding: 0 0 1.25rem 0;\n  }\n\n  .cx-card-actions {\n    display: flex;\n    justify-content: flex-end;\n    padding: 1.25rem 0 0 0;\n  }\n\n  .cx-card-link {\n    margin-inline-start: 1rem;\n  }\n}\n","%cx-pagination {\n  display: flex;\n  justify-content: flex-start;\n\n  align-content: center;\n  align-items: stretch;\n\n  a {\n    color: var(--cx-color-text);\n    width: 48px;\n    height: 46px;\n\n    border: solid 1px var(--cx-color-light);\n    box-sizing: content-box;\n\n    border-inline-end: none;\n\n    display: flex;\n    align-items: center;\n    justify-content: center;\n\n    transition: background-color 0.3s, color 0.3s;\n\n    &:first-child {\n      border-top-left-radius: 0.25rem;\n      border-bottom-left-radius: 0.25rem;\n    }\n    &:last-child {\n      border-inline-end: solid 1px var(--cx-color-light);\n      border-top-right-radius: 0.25rem;\n      border-bottom-right-radius: 0.25rem;\n    }\n\n    &.current {\n      background-color: var(--cx-color-primary);\n      border-color: var(--cx-color-primary);\n      color: var(--cx-color-inverse);\n      opacity: 1;\n    }\n\n    &.current,\n    &.disabled,\n    &.gap {\n      pointer-events: none;\n    }\n\n    &:hover {\n      text-decoration: none;\n      background-color: var(--cx-color-background);\n    }\n    &.current + a {\n      border-inline-start: none;\n    }\n    &:focus {\n      z-index: 1;\n    }\n  }\n}\n","/* IDEAS TO EXTEND\n  - sizing (variants: small, medium, big)\n  - theme (variants: primary, secondary)\n*/\n\n:root {\n  --cx-spinner-radius: 120px;\n  --cx-spinner-border-width: 15px;\n  --cx-spinner-animation-time: 1s;\n  --cx-spinner-primary-color: var(--cx-color-primary);\n  --cx-spinner-secondary-color: var(--cx-color-light);\n}\n\n%cx-spinner {\n  .loader-container {\n    position: relative;\n    margin-top: 30px;\n    margin-inline-end: auto;\n    margin-bottom: 30px;\n    margin-inline-start: auto;\n    width: var(--cx-spinner-radius);\n    height: var(--cx-spinner-radius);\n  }\n  .loader {\n    width: var(--cx-spinner-radius);\n    height: var(--cx-spinner-radius);\n    font-size: 10px;\n    overflow: hidden;\n    animation: load8 var(--cx-spinner-animation-time) infinite linear;\n\n    // @TODO consider merging with the %spinner from the _animation scss\n    &::before,\n    &:before {\n      content: '';\n      box-sizing: border-box;\n      display: block;\n      width: var(--cx-spinner-radius);\n      height: var(--cx-spinner-radius);\n      border: var(--cx-spinner-border-width) solid;\n      margin: auto;\n      border-top-color: var(--cx-spinner-secondary-color);\n      border-bottom-color: var(--cx-spinner-secondary-color);\n      border-right-color: var(--cx-spinner-secondary-color);\n      border-left-color: var(--cx-spinner-primary-color);\n      border-radius: 50%;\n\n      // We prevent pointer events on this pseudo class to avoid any direct mouse\n      // interaction. Mouse interaction with the spinner class affects developer\n      // experience badly, as you can't inspect the actual elements. Also, users\n      // would be blocked to use the context menu to open a link or image in a\n      // new tab.\n      pointer-events: none;\n    }\n  }\n  @keyframes load8 {\n    0% {\n      transform: rotate(0deg);\n    }\n    100% {\n      transform: rotate(360deg);\n    }\n  }\n}\n","// Icons in an Right to Left context might require to be flipped.\n// Most icons will remain as-is, only icons that express a direction (i.e. caret-left)\n// are usually flipped.\n[dir='ltr'] cx-icon.flip-at-ltr,\n[dir='rtl'] cx-icon.flip-at-rtl {\n  transform: scaleX(-1);\n}\n\n.cx-icon {\n  @extend %cx-icon;\n}\n\n%cx-icon {\n  align-self: center;\n  line-height: normal;\n\n  &.small {\n    &:before {\n      font-size: 0.9rem;\n    }\n  }\n\n  svg {\n    width: 28px;\n    height: 28px;\n    fill: currentColor;\n  }\n\n  &.small {\n    svg {\n      width: 14px;\n      height: 14px;\n    }\n  }\n}\n","%cx-table {\n  table {\n    // avoid breaking table content\n    white-space: nowrap;\n    table-layout: fixed;\n    width: 100%;\n    td,\n    th {\n      white-space: nowrap;\n      overflow: hidden;\n      text-overflow: ellipsis;\n    }\n\n    th {\n      padding: var(--cx-spatial-base) var(--cx-spatial-md);\n\n      color: var(--cx-color-secondary);\n      text-transform: uppercase;\n\n      &.sortable {\n        cursor: pointer;\n      }\n    }\n\n    thead tr,\n    > tr:not(:last-child) {\n      border-bottom: solid 1px var(--cx-color-light);\n    }\n\n    th,\n    td {\n      transition: opacity 0.2s ease-in-out 0.2s, width 0.2s ease-in-out 0.2s;\n\n      padding: var(--cx-spatial-md);\n\n      > * {\n        display: flex;\n        // justify-content: space-between;\n      }\n    }\n  }\n}\n","%cx-order-overview {\n  .cx-order-summary {\n    background-color: var(--cx-color-background);\n\n    @include media-breakpoint-down(sm) {\n      background-color: var(--cx-color-inverse);\n    }\n\n    .container {\n      display: flex;\n      flex-wrap: wrap;\n      padding: 15px 0;\n\n      @include media-breakpoint-down(md) {\n        max-width: 100%;\n        min-width: 100%;\n        padding: 0 1.25rem;\n      }\n      @include media-breakpoint-down(sm) {\n        flex-direction: column;\n        padding: 1.25rem;\n      }\n\n      .cx-summary-card {\n        flex: 1;\n        padding: 0 15px;\n\n        &:not(:last-of-type) {\n          @include media-breakpoint-up(lg) {\n            border-inline-end: 1px solid var(--cx-color-text);\n          }\n        }\n\n        @include media-breakpoint-down(md) {\n          flex: 0 0 33%;\n        }\n\n        @include media-breakpoint-down(sm) {\n          flex: 1;\n          background-color: var(--cx-color-inverse);\n          border-width: 1px;\n          border-style: solid;\n          border-color: var(--cx-color-light);\n          margin: 0.625rem 0;\n        }\n\n        .cx-card-title {\n          @include type('4');\n          font-weight: bold;\n        }\n      }\n    }\n  }\n}\n","%cx-login {\n  @include media-breakpoint-down(md) {\n    > a {\n      padding: 1rem;\n      display: block;\n    }\n  }\n\n  .cx-login-greet {\n    color: var(--cx-color-inverse);\n    @include type('7');\n    font-weight: var(--cx-font-weight-semi);\n\n    @include media-breakpoint-down(md) {\n      color: var(--cx-color-text);\n      padding: 0.5rem 1rem 0;\n      @include type();\n    }\n  }\n}\n","%cx-register {\n  form {\n    a {\n      text-decoration: underline;\n    }\n    .cx-login-link {\n      margin: 1rem 0 0;\n    }\n  }\n}\n","%cx-anonymous-consent-management-banner {\n  .anonymous-consent-banner {\n    position: fixed;\n    bottom: 0;\n    width: 100vw;\n    background-color: var(--cx-color-inverse);\n    z-index: 1000;\n    border-top: 5px solid var(--cx-color-primary);\n    color: var(--cx-color-text);\n    padding: 2rem 0;\n  }\n\n  .cx-banner-title {\n    @include type('3');\n    margin-bottom: 0.5rem;\n  }\n\n  .cx-banner-description {\n    @include type();\n  }\n\n  .cx-banner-buttons {\n    text-align: end;\n    button:last-child {\n      margin-inline-start: 1.5rem;\n    }\n    @include media-breakpoint-down(xs) {\n      text-align: center;\n      margin-top: 1rem;\n    }\n    @include media-breakpoint-up(md) {\n      margin-top: 1rem;\n    }\n  }\n\n  .anonymous-consent-banner-hidden {\n    display: none;\n  }\n}\n","@import '../../../theme';\n\n%cx-anonymous-consent-dialog {\n  color: var(--cx-color-text);\n  background-color: rgba(0, 0, 0, 0.5);\n\n  .cx-anonymous-consent-dialog {\n    @extend .modal-dialog;\n    @extend .modal-dialog-centered;\n    @extend .modal-lg;\n\n    .cx-dialog-content {\n      @extend .modal-content;\n\n      .cx-dialog-header {\n        @extend .modal-header;\n        padding-top: 2rem;\n        padding-inline-end: 1.75rem;\n        padding-bottom: 0.85rem;\n        padding-inline-start: 1.75rem;\n      }\n      .cx-dialog-body {\n        @extend .modal-body;\n        padding-top: 0;\n        background-color: var(--cx-color-inverse);\n\n        @include media-breakpoint-only(xs) {\n          padding: 1.5rem;\n          border-top: 1px solid var(--cx-color-light);\n        }\n      }\n      .cx-dialog-buttons {\n        text-align: end;\n        padding: 1rem 4rem;\n        @include media-breakpoint-only(xs) {\n          padding: 1rem;\n        }\n      }\n\n      .cx-action-link {\n        margin: 0 0.35rem;\n      }\n\n      .cx-dialog-row {\n        max-width: 100%;\n        @include media-breakpoint-down(xs) {\n          padding: 0;\n        }\n      }\n\n      h3 {\n        @include type('3');\n      }\n\n      .cx-dialog-description {\n        @include type();\n        padding: 1.5rem 1.75rem 0;\n      }\n\n      a:not([href]):not([tabindex]) {\n        text-decoration: underline;\n      }\n    }\n  }\n}\n","%cx-anonymous-consent-open-dialog {\n  display: flex;\n  justify-content: center;\n  margin: 0 3vw 3vw 3vw;\n\n  @include media-breakpoint-down(sm) {\n    justify-content: flex-start;\n  }\n  .btn-link {\n    padding: 0;\n    color: var(--cx-color-inverse);\n    font-size: 0.8rem;\n    &:n